import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private http: HttpClient) { }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      console.error(error); // log to console instead
  
      return of(result as T);
    };
  }

  getNews(search: string): Observable<any> {
    return this.http.get("http://newsapi.org/v2/everything?q="+search+"&from=2020-11-02&sortBy=publishedAt&apiKey=0b73d59bb8114d7991582ea5a3a81c13").pipe(
      catchError(this.handleError<any>('getProjects', []))
    );
  }

  getTweets(search: string, start, rows): Observable<any> {
    return this.http.get("http://ec2-3-227-30-89.compute-1.amazonaws.com:8983/solr/tweets/select?q=tweet_text%3A"+search+"&rows="+rows.toString()+"&start="+start).pipe(
      catchError(this.handleError<any>('getProjects', []))
    );
  }

}
